import * as singleSpa from "single-spa";
import { enableProdMode } from "@angular/core";

// TODO: DESCOMENTAR EN PRODU
enableProdMode();

const applications = [
  {
    name: "@mbc/general-mf",
    activeWhen: ["#/"],
  },
  {
    name: "@mbc/tasks-mf",
    activeWhen: ["#/tasks-mf"],
  },
  {
    name: "@mbc/authentication-mf",
    activeWhen: ["#/authentication-mf"],
  },
];

const loadMf = (mf) => {
  return Promise.resolve()
    .then(() => {
      document.getElementById("single-spa-loader-app").style.display = "block";
      return System.import(mf);
    })
    .then((app) => {
      document.getElementById("single-spa-loader-app").style.display = "none";
      return app;
    });
};

const registerSingleSpaApplications = (applications) => {
  for (const app of applications) {
    singleSpa.registerApplication({
      name: app.name,
      app: () => loadMf(app.name),
      activeWhen: app.activeWhen,
    });
  }
};

registerSingleSpaApplications(applications);

singleSpa.start({
  urlRerouteOnly: true,
});
